<script>
  import {getData} from '../../api/index/index.js'
  export default{

  mounted(){

      //getData().then(res => {
        //console.log(res);
        this.mapinit(this.map);
      //})
  },
  data(){
    return{
      map: {
	"city": [{
		"name": "北京",
		"value": [116.24, 39.55]
	},{
		"name": "深圳",
		"value": [114.271522, 22.753644]
	},{
		"name": "乌鲁木齐",
		"value": [87.627447,43.834612]
	},{
		"name": "上海",
		"value": [121.449153,31.22741]
	}],
	"where": [
		{
			"fromName": "深圳",
			"toName": "乌鲁木齐",
			"type": "暴力破解",
			"coords": [
			    [114.271522, 22.753644],
			    [87.627447,43.834612]
			]
		},{
			"fromName": "北京",
			"toName": "上海",
			"type": "Web攻击",
			"coords": [
			    [116.24, 39.55],
			    [121.449153,31.22741]
			]
		},{
			"fromName": "乌鲁木齐",
			"toName": "北京",
			"type": "DDoS",
			"coords": [
			    [87.627447,43.834612],
			    [116.24, 39.55]
			]
		},{
			"fromName": "乌鲁木齐",
			"toName": "北京",
			"type": "DDoS",
			"coords": [
			    [87.627447,43.834612],
			    [116.24, 39.55]
			]
		},{
			"fromName": "北京",
			"toName": "深圳",
			"type": "漏洞攻击",
			"coords": [
			    [116.24, 39.55],
			    [114.271522, 22.753644]
			]
		},{
			"fromName": "北京",
			"toName": "深圳",
			"type": "DDoS",
			"coords": [
			    [116.24, 39.55],
			    [114.271522, 22.753644]
			]
		},{
			"fromName": "北京",
			"toName": "深圳",
			"type": "后门木马",
			"coords": [
			    [116.24, 39.55],
			    [114.271522, 22.753644]
			]
		},{
			"fromName": "深圳",
			"toName": "北京",
			"type": "异常行为",
			"coords": [
			    [114.271522, 22.753644],
				[116.24, 39.55]
			]
		},{
			"fromName": "深圳",
			"toName": "北京",
			"type": "漏洞攻击",
			"coords": [
			    [114.271522, 22.753644],
				[116.24, 39.55]
			]
		},{
			"fromName": "深圳",
			"toName": "北京",
			"type": "命令与控制",
			"coords": [
			    [114.271522, 22.753644],
				[116.24, 39.55]
			]
		},{
			"fromName": "深圳",
			"toName": "乌鲁木齐",
			"type": "僵尸主机",
			"coords": [
			    [114.271522, 22.753644],
			    [87.627447,43.834612]
			]
		}
	]
}
    }
  },
  methods: {
    mapinit(res){
      let myChart = this.$echarts.init(this.$refs.main);
          let legend = []
          let color = [
            {
              type: '暴力破解',
              color: '#ff7b00'
            },{
              type: '命令与控制',
              color: '#7d8ef8'
            },{
              type: 'Web攻击',
              color: '#ffcf00'
            },{
              type: 'DDoS',
              color: '#3ec0e0'
            },{
              type: '后门木马',
              color: '#3ec0e0'
            },{
              type: '漏洞攻击',
              color: '#01c3a9'
            },{
              type: '僵尸主机',
              color: '#009bff'
            },{
              type: '异常行为',
              color: '#0ec6e2'
            }
          ]

          function getcolor(type){//获取颜色
            let color1 = '';
            color.forEach(item => {
              if(item.type == type){
                color1 = item.color;
              }
            })
            return color1;
          }

           let option = {
               backgroundColor: 'transparent',
               legend: {
                orient: 'horizontal',
                bottom:'8%',
                right:'center',
                 data: legend,
                 textStyle: {
                     color: '#fff'
                 },
                 selectedMode: 'multiple',
               },
               geo: {
                   map: 'china',
                   aspectScale: 0.85,
                   layoutCenter: ["50%", "50%"], //地图位置
                   layoutSize: '100%',
                   itemStyle: {
                       normal: {
                           shadowColor: '#276fce',
                           shadowOffsetX: 0,
                           shadowOffsetY: 15,
                           opacity: 0.5,
                       },
                       emphasis: {
                           areaColor: '#276fce',
                       }
                   },
                   regions: [{
                       name: '南海诸岛',
                       itemStyle: {
                           areaColor: 'rgba(0, 10, 52, 1)',
                           borderColor: 'rgba(0, 10, 52, 1)',
                           normal: {
                               opacity: 0,
                               label: {
                                   show: false,
                                   color: "#009cc9",
                               }
                           },
                       },
                       label: {
                           show: false,
                           color: '#FFFFFF',
                           fontSize: 12,
                       },
                   }],

               },
               series: [
                   // 常规地图
                   {
                       type: 'map',
                       mapType: 'china',
                       aspectScale: 0.85,
                       layoutCenter: ["50%", "50%"], //地图位置
                       layoutSize: '100%',
                       zoom: 1, //当前视角的缩放比例
                       itemStyle: {
                           normal: {
                               areaColor: '#0c274b',
                               borderColor: '#1cccff',
                               borderWidth: 1.5
                           },
                           emphasis: {
                               areaColor: '#02102b',
                               label: {
                                   color: "#fff"
                               }
                           }
                       },


                   },
                   // 区域散点图
                   {
                       type: 'effectScatter',
                       coordinateSystem: 'geo',
                       zlevel: 2,
                       symbolSize: 10,
                       rippleEffect: { //坐标点动画
                           period: 3,
                           scale: 5,
                           brushType: 'fill'
                       },
                       label: {
                           normal: {
                               show: true,
                               position: 'right',
                               formatter: '{b}',
                               color: '#b3e2f2',
                               fontWeight: "bold",
                               fontSize: 18
                           }
                       },
                       data: res.city,
                       itemStyle: { //坐标点颜色
                           normal: {
                               show: true,
                               color: 'green',
                               shadowBlur: 20,
                               shadowColor: '#fff'
                           },
                           emphasis: {
                               areaColor: '#f00'
                           }
                       },
                       symbolSize: 5

                   },
                   // 线 和 点

               ]
           };


           let arr = [];
           let curveness = 0.3;
           let coords = [];
           let curcurveness = curveness;
           res.where.forEach((item,index) => {
             curcurveness = curveness;
             coords.forEach((items,indexs) => {
               if(items.toName == item.toName && items.fromName == item.fromName){
                  items.curveness+=0.1;
                  curcurveness = items.curveness;
               }
             })
             coords.push({
               fromName: item.fromName,
               toName: item.toName,
               curveness: curcurveness
             })
             legend.push(item.type);
             arr.push({
                 name: item.type,
                 type: 'lines',
                 zlevel: 2,
                 effect: {
                     show: true,
                     period: 4, //箭头指向速度，值越小速度越快
                     trailLength: 0.2, //特效尾迹长度[0,1]值越大，尾迹越长重
                     symbol: 'arrow', //箭头图标
                     symbolSize: 7, //图标大小
                 },
                 lineStyle: {
                     normal: {
                         color: getcolor(item.type),
                         width: 1, //线条宽度
                         opacity: 0.1, //尾迹线条透明度
                         curveness:curcurveness //尾迹线条曲直度
                     }
                 },
                 data: [{
                   fromName: item.fromName,
                   toName: item.toName,
                   coords: item.coords
               }]
             })
           })


          option.series = option.series.concat(arr);
          myChart.setOption(option);

    }
  }
}
</script>

