<template>
    <div class="radar" ref='radar'>
      <span>{{num}}</span>
      <img src="../../assets/imgs/index/1.png" alt="" :style='{transform: "rotate("+deg+"deg)"}'>
      <p class="dian" v-for='(item,index) in arr' :key='index' :style='{top: item.top+"px",left: item.left+"px"}'></p>
    </div>
</template>

<script>
  export default{
    data(){
      return{
        left: 0,
        top: 0,
        num: 5,
        arr: [],
        deg: 0
      }
    },
    mounted(){
      let arr = [];
      for(let i = 0;i<this.num;i++){
        arr.push({
          left: this.left = this.randomNum(50,250),
          top: this.top = this.randomNum(25,210)
        })
      }
      this.arr = arr;
      setInterval(() => {
        this.deg++;
        if(this.deg == 360){
          this.deg = 0;
        }
      },100)
      setInterval(() => {
        arr = []
        for(let i = 0;i<this.num;i++){
          arr.push({
            left: this.left = this.randomNum(50,250),
            top: this.top = this.randomNum(25,210)
          })
        }
        this.arr = arr;
      },10000)
    },
    methods: {
      randomNum(minNum,maxNum){
          switch(arguments.length){
              case 1:
                  return parseInt(Math.random()*minNum+1,10);
              break;
              case 2:
                  return parseInt(Math.random()*(maxNum-minNum+1)+minNum,10);
              break;
                  default:
                      return 0;
                  break;
          }
      }
    }
  }
</script>

<style lang='less'>
.radar{
  position: relative;
  height: 270px;
  .dian{
    width: 0px;
    height: 0px;
    background: #fff;
    border-radius: 50px;
    position: absolute;
    top: 50px;
    left: 50px;
    box-shadow: 0 0 5px 3px #fff,0 0 10px 8px red;
  }
  img{
    width: 258px;
    height: 169px;
    position: absolute;
    left: 30px;
    top: 40px;
  }
  span{
    position: absolute;
    top: 38%;
    left: 35%;
    color: #fff;
    z-index: 99;
    font-size: 35px;
  }
  @keyframes move{
    0%{
      transform: rotate(0deg);
    }
    100%{
      transform: rotate(360deg);
    }
  }
}
</style>
